        p.mb-0 La codorniz japonesa (Coturnix coturnix japonica) es una especie aviar menor de gran importancia econmica por su rpido crecimiento, alta eficiencia productiva y bajo requerimiento de espacio, lo que la convierte en una excelente opcin para sistemas intensivos, familiares o de economa campesina. En Colombia, la coturnicultura ha ganado terreno especialmente en departamentos como Antioquia, Cundinamarca y Valle del Cauca, consolidndose como una alternativa rentable y sostenible dentro de las especies menores.
    
    .titulo-con-imagen.mb-5(data-aos="fade-right")
