                    p.mb-0 Lneas de carne: aves de mayor peso corporal, crecimiento acelerado y mejor rendimiento de canal.
                p La seleccin considera la adaptacin al clima local, la sanidad del plantel y el cumplimiento de las normas de bioseguridad (Resolucin ICA 68167 de 2020).
