    #t_2_6.titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 2.6 Ciclo productivo de la cabra (Capra hircus)

    p.mb-5 La cabra (Capra hircus) es una especie menor de alta versatilidad productiva, aprovechada para la obtencin de carne, leche, piel y fibra. Su capacidad de adaptacin a condiciones climticas adversas y su eficiencia alimenticia la convierten en una especie clave para los sistemas familiares, semi intensivos y extensivos, especialmente en regiones ridas y semiridas de Colombia como La Guajira, Huila, Cesar y Norte de Santander.

    .row.mb-5
      .col-lg-5.mb-3.mb-lg-0
        figure
          img(src="@/assets/curso/tema2/54.png", data-aos="zoom-in")
      .col-lg-7
        .bg-color-15.tarjeta.p-4(data-aos="fade-left")
          img(src="@/assets/curso/tema2/55.svg", style="max-width: 90px").mb-3
          p.mb-0 El ciclo productivo de la cabra vara segn el sistema de manejo, pero en promedio abarca de 8 a 10 meses entre el empadre y la finalizacin del periodo de lactancia. Este proceso puede organizarse tcnicamente para lograr tres cras en dos aos, optimizando el uso del hato reproductivo y favoreciendo la sostenibilidad productiva.
    
    .titulo-con-imagen.mb-5(data-aos="fade-right")
      .titulo-con-imagen__fondo
        img.titulo-con-imagen__imagen(src="@/assets/componentes/1.svg")
        h3.titulo-con-imagen__texto Duracin general del ciclo
    
    p.mb-5 El ciclo biolgico y productivo de la cabra est determinado por su capacidad reproductiva y el tipo de explotacin (carne, leche o doble propsito). A continuacin, se presentan los parmetros promedio observados en los sistemas caprinos colombianos.

    .bg-full-width.bg-color-7
      .px-4.px-md-5
