                        i.fa-solid.fa-circle-check
                        p.mb-0 <b>Duracin del ciclo</b>: 8-10 meses.
                      li.mb-3.d-flex
                        i.fa-solid.fa-circle-check
                        p.mb-0 <b>Produccin anual:</b> 1-2 partos por hembra (hasta 3 cras en 2 aos con buena planificacin).
                      li.mb-3.d-flex
                        i.fa-solid.fa-circle-check
                        p.mb-0 <b>Vida til reproductiva</b>: 6-8 aos.
                      
    .titulo-con-imagen.mb-5(data-aos="fade-right")
