    p.mb-5 El ciclo productivo de la cabra se compone de una secuencia de fases biolgicas y productivas interdependientes. Cada etapa requiere condiciones tcnicas especficas de alimentacin, sanidad y manejo para garantizar una produccin eficiente y sostenible.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-7
