    .bg-full-width.bg-fondo-slider.mb-5
      .p-4.p-md-5
        .tarjeta.bg-color-16.p-4
          PasosB.color-acento-contenido
            .row(titulo="Paso 1").p-5
              .col-lg-7.mb-4.mb-md-0
                h5.mb-5 Seleccin de reproductores
                p Se seleccionan machos y hembras de razas rsticas o especializadas segn el objetivo productivo (carne, leche o doble propsito). Los criterios incluyen buen estado corporal, alta fertilidad, antecedentes de partos exitosos, buena conformacin mamaria en cabras lecheras y ausencia de defectos genticos o fsicos. En Colombia predominan razas criollas, <em>Anglonubia, Alpina, Saanen y Boer</em>, adaptadas a sistemas familiares o de pastoreo bajo condiciones tropicales.
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema2/58.png', alt='Texto que describa la imagen')
            .row(titulo="Paso 2").p-5
              .col-lg-7.mb-4.mb-md-0
                h5.mb-5 Empadre / monta / cobertura
                p El empadre puede ser natural continuo (convivencia constante con el macho) o controlado/estacional, programado para coincidir con pocas de buena oferta de forraje. La relacin macho:hembra recomendada es 1:25. La edad de primera monta es de 8-10 meses, siempre que el animal alcance al menos el 60 % del peso adulto. Con buena planificacin alimenticia y sanitaria, es posible obtener tres partos en dos aos (CONtexto Ganadero, 2023).
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema2/59.png', alt='Texto que describa la imagen')
            .row(titulo="Paso 3").p-5
              .col-lg-7.mb-4.mb-md-0
                h5.mb-5 Gestacin
                p La gestacin dura entre 145 y 155 das ( 5 meses). El nmero de cras por parto oscila entre 1 y 3, dependiendo de la raza y el manejo nutricional. Durante esta etapa se debe garantizar una dieta balanceada y controlar enfermedades parasitarias y abortivas. Se recomienda suplementacin energtica y proteica en el ltimo tercio del embarazo para evitar partos distcicos y mejorar la vitalidad de las cras.
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema2/60.png', alt='Texto que describa la imagen')
            .row(titulo="Paso 4").p-5
              .col-lg-7.mb-4.mb-md-0
                h5.mb-5 Parto
                p El parto es una fase crtica del ciclo. Debe llevarse a cabo en instalaciones limpias, secas y tranquilas, con supervisin para evitar complicaciones. La atencin posparto incluye la limpieza del ombligo con antisptico, asegurar el calostrado en las primeras seis horas de vida y el monitoreo del estado corporal de la madre.
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema2/61.png', alt='Texto que describa la imagen')
            .row(titulo="Paso 5").p-5
              .col-lg-7.mb-4.mb-md-0
                h5.mb-5 Lactancia y crecimiento de cras
                p La lactancia dura entre 60 y 90 das. Durante este periodo, los cabritos maman y comienzan a consumir forraje tierno o concentrado iniciador. En cabras lecheras se realiza el ordeo diario posterior al amamantamiento parcial o tras destete temprano. En sistemas de doble propsito, parte de la leche se destina al consumo humano y parte se reserva para el desarrollo de las cras. El destete se lleva a cabo cuando los cabritos alcanzan entre 8 y 12 kg de peso vivo.
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema2/62.png', alt='Texto que describa la imagen')
            .row(titulo="Paso 6").p-5
              .col-lg-7.mb-4.mb-md-0
                h5.mb-5 Recra / engorde / produccin de leche
                p En sistemas de carne, los cabritos se engordan hasta alcanzar 25-30 kg, momento ideal para su venta o sacrificio. En sistemas lecheros, la cabra entra en fase de produccin de leche que puede extenderse de 120 a 300 das, segn la raza y el manejo. Se recomienda un intervalo servicio-parto de aproximadamente 45 das en sistemas intensivos para conservar la eficiencia reproductiva. En Colombia, las razas Saanen y Alpina alcanzan producciones de 250-350 litros por lactancia, mientras que las razas criollas producen entre 80 y 150 litros en sistemas familiares.
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema2/63.png', alt='Texto que describa la imagen')
            .row(titulo="Paso 6").p-5
              .col-lg-7.mb-4.mb-md-0
                h5.mb-5 Finalizacin y reemplazo
                p Al finalizar la lactancia o cuando la cabra presenta una disminucin en su productividad, se procede a su reemplazo. Se seleccionan hembras jvenes de alto potencial gentico, y se descartan los animales con enfermedades o bajo rendimiento. Tambin se comercializa la carne, la leche excedente y los subproductos como piel, estircol o quesos artesanales. El ciclo reproductivo puede repetirse cada 8-10 meses en unidades familiares o semiintensivas.
              .col-lg-5
